// Cada 260 días el calendario regresa a su punto de origen
const fullTurn = 260;

// Calcula los datos antes del punto de referencia
const beforeReferencePoint = (point, newDate, millisecondsOfADay) => {
  let data = {};
  let daysFromPoint = (Math.round((point - newDate) / millisecondsOfADay)) % fullTurn;
  
  /**
   * 
   */
  if (daysFromPoint > 90) {
    data.dayZapoteco = 8 - (daysFromPoint - 2) % 9;
  } else {
    data.dayZapoteco = 8 - (daysFromPoint - 1) % 9;
  }
  data.weekZapoteco = 3 - Math.trunc(daysFromPoint / 13) % 4;
  data.dayOfWeek = 12 - (daysFromPoint - 1) % 13;
  
  return data;
};

// Calcula los datos después del punto de referencia
const afterReferencePoint = (point, newDate, millisecondsOfADay) => {
  const daysFromPoint = (Math.round((newDate - point) / millisecondsOfADay)) % fullTurn;
  let data = {};

  /**
   * La semana 14 utiliza 14 nombres de los días en zapoteco.
   * Con respecto al punto de referencia.
   */
  if (daysFromPoint > 170) {
    data.dayZapoteco = (daysFromPoint + 1) % 9;
  } else {
    data.dayZapoteco = daysFromPoint % 9;
  }
  data.weekZapoteco = Math.trunc(daysFromPoint / 13) % 4;
  data.dayOfWeek = daysFromPoint % 13;

  return data;
};

const algorithms = {
  beforeReferencePoint,
  afterReferencePoint
};

export default algorithms;