import React, { useState } from 'react';
import c from 'calendar';

import './css/calendar.css';
import CardDay from './components/CardDay';
import InfoModal from './components/InfoModal';
import data from './data/data';
import next from './images/next.svg';
import back from './images/back.svg';
import buttonHelp from './images/button-help.svg';
import algorithms from './algorithms/algorithms';

const cal = new c.Calendar(1);
const currentDate = new Date();


const App = () => {
  const [date, setDate] = useState({
    year: currentDate.getFullYear(),
    month: currentDate.getMonth()
  });

  const handleNextMonth = () => {
    if (date.month === 11)
      setDate({ year: date.year + 1, month: 0 });
    else
      setDate({ ...date, month: date.month + 1 });
  };

  const handleBackMonth = () => {
    if (date.month === 0)
      setDate({ year: date.year - 1, month: 11 });
    else
      setDate({ ...date, month: date.month - 1 });
  };

  const _key = date.year.toString() + date.month;
  const _newDate = new Date(date.year, date.month, 1, 0, 0, 0, 0);
  let _data = {};

  if (data.dateReference > _newDate) {
    _data = algorithms.beforeReferencePoint(
      data.dateReference,
      _newDate,
      data.millisecondsOfADay
    );
  } else {
    _data = algorithms.afterReferencePoint(
      data.dateReference,
      _newDate,
      data.millisecondsOfADay
    );
  }

  return (
    <main>
      <div className="calendar-header">
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <button
            type="button"
            className="calendar-button"
            style={{ height: '35px', padding: '0 10px' }}
            data-bs-toggle="modal"
            data-bs-target="#modal-help"
          >
            <img
              src={buttonHelp}
              className="img-fluid"
              alt="Button Help"
              style={{ height: '35px' }}
            />
          </button>
          <div className="calendar-actions">
            <div
              className="d-flex justify-content-between align-items-center flex-grow-1"
            >
              <button
                type="button"
                className="calendar-button"
                onClick={handleBackMonth}
              >
                <img
                  src={back}
                  className="img-fluid"
                  alt="Button Back"
                  style={{ height: 'var(--height-button)' }}
                />
              </button>
              <span
                className="calendar-header-title"
              >{data.months[date.month] + ' ' + date.year}</span>
              <button
                type="button"
                className="calendar-button"
                onClick={handleNextMonth}
              >
                <img
                  src={next}
                  className="img-fluid"
                  alt="Button Next"
                  style={{ height: 'var(--height-button)' }}
                />
              </button>
            </div>
          </div>
        </div>
        <div className="calendar-info-weeks">{
          data.weeksZapoteco.map(week =>
            <div key={week.name} className="calendar-info-week">
              <div className="calendar-week-type"
                style={{ backgroundColor: week.color }}></div>
              <span>{week.name}</span>
            </div>)
        }</div>
        <div className="calendar-name-days" >
          {data.days.map(day => <span className="bold-text" key={day}>{day}</span>)}
        </div>
      </div>
      <div className="calendar-date">{
        cal.monthDays(date.year, date.month).map((week, i) =>
          <div key={i} className="calendar-week">{
            week.map(day => {
              if (day > 0) {
                let card = null;
                if (_data.weekZapoteco === 1
                    && _data.dayZapoteco === 7
                    && _data.dayOfWeek === 0) {
                  const dayZapoteco = {
                    name: data.daysZapoteco[7].name +
                          ' - ' + data.daysZapoteco[8].name,
                    meaning: data.daysZapoteco[7].meaning +
                          ' - ' + data.daysZapoteco[8].meaning
                  };
                  card = <CardDay key={_key + day} day={day}
                    weekColor={data.weeksZapoteco[1].color}
                    dayOfWeek={_data.dayOfWeek + 1}
                    dayZapoteco={dayZapoteco}
                  />;
                  _data.dayZapoteco = 0;
                } else {
                  card = <CardDay key={_key + day} day={day}
                    weekColor={data.weeksZapoteco[_data.weekZapoteco].color}
                    dayOfWeek={_data.dayOfWeek + 1}
                    dayZapoteco={data.daysZapoteco[_data.dayZapoteco]}
                  />;
                  _data.dayZapoteco = (_data.dayZapoteco + 1) % 9;
                }
                _data.dayOfWeek++;
                _data.weekZapoteco = (_data.weekZapoteco + Math.trunc(_data.dayOfWeek / 13)) % 4;
                _data.dayOfWeek %= 13;
                return card;
              } else
                return null;
            })
          }</div>
        )
      }</div>
      <InfoModal />
    </main>
  );
};

export default App;
